import React, { useEffect, useState } from "react";
import {
  MenuItem,
  FormControl,
  Select,
  Card,
  CardContent,
} from "@material-ui/core";
import {
  Presets,
  ComponentTransition,
  AnimationTypes,
} from "react-component-transition";
import { sortData } from "../utils/sortData";
import { prettyPrint } from "../utils/prettyPrint";
import InfoBox from "../components/InfoBox";
import LineGraph from "../components/LineGraph";
import Table from "../components/Table";
import NavBar from "../components/NavBar";
import Maps from "../components/Map";
import MapDark from "../components/MapDark";
import DarkModeToggle from "react-dark-mode-toggle";
import useDarkMode from "../utils/useDarkMode";
import "leaflet/dist/leaflet.css";

const styles = (theme) => ({
  select: {
    "&:before": {
      borderColor: "red",
    },
  },
});

function App({ classes }) {
  const [countries, setCountries] = useState([]);
  const [country, setCountry] = useState("worldwide");
  const [countryInfo, setCountryInfo] = useState([]);
  const [tableData, setTableData] = useState([]);
  const [mapCenter, setMapCenter] = useState({ lat: 34.80746, lng: -40.4796 });
  const [mapZoom, setMapZoom] = useState(3);
  const [mapCountries, setMapCountries] = useState([]);
  const [casesType, setCasesType] = useState("cases");
  const [colorTheme, setTheme] = useDarkMode();
  const [isDarkMode, setIsDarkMode] = useState(() => false);

  useEffect(() => {
    fetch("https://disease.sh/v3/covid-19/all")
      .then((response) => response.json())
      .then((data) => {
        setCountryInfo(data);
      });
  }, []);

  useEffect(() => {
    const getCountriesData = async () => {
      await fetch("https://disease.sh/v3/covid-19/countries")
        .then((response) => response.json())
        .then((data) => {
          const countries = data.map((country) => ({
            name: country.country,
            value: country.countryInfo.iso2,
          }));

          const sortedData = sortData(data);
          setTableData(sortedData);
          setMapCountries(data);
          setCountries(countries);
        });
    };

    getCountriesData();
  }, []);

  const onCountryChange = async (event) => {
    const countryCode = event.target.value;
    setCountry(countryCode);

    const url =
      countryCode === "worldwide"
        ? "https://disease.sh/v3/covid-19/all"
        : `https://disease.sh/v3/covid-19/countries/${countryCode}`;

    await fetch(url)
      .then((response) => response.json())
      .then((data) => {
        setCountry(countryCode);
        setCountryInfo(data);

        setMapCenter([data.countryInfo.lat, data.countryInfo.long]);
        setMapZoom(6);
      });
  };

  const map =
    colorTheme === "dark" ? (
      <Maps
        center={mapCenter}
        zoom={mapZoom}
        countries={mapCountries}
        casesType={casesType}
        className="transition duration-1000"
      />
    ) : (
      <MapDark
        center={mapCenter}
        zoom={mapZoom}
        countries={mapCountries}
        casesType={casesType}
        className="transition duration-1000"
      />
    );

  return (
    <div className="relative dark:bg-darkModeBackground dark:text-white h-screen w-screen transition duration-1000">
      <NavBar>
        <button
          onClick={() => setTheme(colorTheme)}
          className="flex flex-row rounded-full items-center color-white cursor-pointer active:outline-none  font-semibold hover:bg-black-translucent"
        >
          <DarkModeToggle
            size={80}
            onChange={setIsDarkMode}
            checked={isDarkMode}
          ></DarkModeToggle>
        </button>
      </NavBar>
      <div className="flex flex-col lg:flex-row justify-evenly p-5 dark:bg-darkModeBackground ">
        <div className="flex-left-side mb-10 lg:mb-0 transition duration-1000">
          <div className="flex justify-center lg:justify-between items-center mb-6 sm:mb-12">
            <form>
              <select
                value={country}
                onChange={onCountryChange}
                className="w-32 bg-transparent border-b-2 border-darkModeBackground dark:border-oddPairWhite p-2 dark:text-white active:border-blue-500 focus:border-blue-500 "
              >
                <option value="worldwide" className="text-black">
                  Worldwide
                </option>
                {countries.map((country, index) => (
                  <option
                    key={index}
                    value={country.value}
                    className="text-black"
                  >
                    {country.name}
                  </option>
                ))}
              </select>
            </form>
          </div>

          <div className="flex justify-between flex-col sm:flex-row">
            <InfoBox
              onClick={(e) => setCasesType("cases")}
              title="Cases"
              active={casesType === "cases"}
              cases={prettyPrint(countryInfo.todayCases)}
              total={prettyPrint(countryInfo.cases)}
            ></InfoBox>
            <InfoBox
              onClick={(e) => setCasesType("recovered")}
              title="Recovered"
              active={casesType === "recovered"}
              cases={prettyPrint(countryInfo.todayRecovered)}
              total={prettyPrint(countryInfo.recovered)}
            ></InfoBox>
            <InfoBox
              onClick={(e) => setCasesType("deaths")}
              title="Deaths"
              active={casesType === "deaths"}
              cases={prettyPrint(countryInfo.todayDeaths)}
              total={prettyPrint(countryInfo.deaths)}
            ></InfoBox>
          </div>
          <ComponentTransition
            enterAnimation={[
              AnimationTypes.scale.enter,
              // AnimationTypes.fade.enter,
              {
                options: {
                  duration: 300,
                },
              },
            ]}
            exitAnimation={[
              AnimationTypes.scale.exit,
              // AnimationTypes.fade.enter,
              {
                options: {
                  duration: 300,
                },
              },
            ]}
          >
            {map}
          </ComponentTransition>
        </div>
        <Card className="dark:bg-darkModeCards">
          <CardContent className="dark:bg-darkModeCards dark:text-white">
            <h1 className="text-2xl font-bold text-center">
              Live Cases by Country
            </h1>

            <Table countries={tableData} />

            <h1 className="text-2xl font-bold mt-5 text-center">
              Worldwide New{" "}
              {casesType.charAt(0).toUpperCase() + casesType.substring(1)}
            </h1>
            <LineGraph casesType={casesType} />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}

export default App;
