import { Card, CardContent, Typography } from "@material-ui/core";
import React from "react";
import useDarkMode from "../utils/useDarkMode";

function InfoBox({ title, cases, total, active, casesType, ...props }) {
  const [colorTheme, setTheme] = useDarkMode();

  let colors = "";

  if (title === "Cases") {
    colors = "border-t-4 border-red-500";
  } else if (title === "Recovered") {
    colors = "border-t-4 border-green-500";
  } else if (title === "Deaths") {
    colors = "border-t-4 border-blue-500";
  }

  return (
    <>
      <Card
        onClick={props.onClick}
        className={`dark:bg-darkModeCards dark:text-white  flex-1 cursor-pointer mt-6 sm:mt-0 min-w-full sm:min-w-0`}
        id="infoBox"
      >
        <CardContent className={`${active && colors}`}>
          <Typography color="textSecondary" className="dark:text-textDarkMode">
            {title}
          </Typography>
          <h1 className={`cases`}>{cases}</h1>
          <Typography
            color="textSecondary"
            className="text-total total dark:text-textDarkMode"
          >
            {total} Total
          </Typography>
        </CardContent>
      </Card>
    </>
  );
}

export default InfoBox;
