import React, { useEffect, useState } from "react";
import { MapContainer, TileLayer, useMap, LeafletMap } from "react-leaflet";
import ReactLeafletGoogleLayer from "react-leaflet-google-layer";
import { showDataOnMap } from "../utils/showDataOnMap";
import useDarkMode from "../utils/useDarkMode";
import ReactDOM from "react-dom";

function ChangeMap({ center, zoom }) {
  const map = useMap();
  map.setView(center, zoom);
  return null;
}

function Maps({ center, zoom, countries, casesType }) {
  const [colorTheme, setTheme] = useDarkMode();

  return (
    <div
      className={`h-96 bg-white p-4 rounded-2xl mt-4 shadow-lg map dark:bg-darkModeCards transition duration-1000`}
      id="map"
    >
      <MapContainer center={center} zoom={zoom}>
        <ChangeMap center={center} zoom={zoom} />
        {/* {root.className === "dark" ? (
          
        ) : (
          <TileLayer url="https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png" />
        )} */}
        <TileLayer url="https://tiles.stadiamaps.com/tiles/alidade_smooth/{z}/{x}/{y}{r}.png" />

        {showDataOnMap(countries, casesType)}
      </MapContainer>
    </div>
  );
}

export default Maps;
